import React from "react";
import styled from "styled-components";
import useAxios from "../api/Axios";
import {
  createSearchParams,
  useLocation,
  useNavigate,
  useParams,
} from "react-router-dom";
const ClassInformation = React.lazy(() =>
  import("../components/assets/ClassInformation")
);

const Dashboard = () => {
  const axios = useAxios();
  const navigate = useNavigate();
  const location = useLocation();
  const [classInfo, setClassInfo] = React.useState([]);
  const param = useParams();
  React.useEffect(() => {
    const getClass = async () => {
      const url = "/all?id=" + param.tid;
      await axios.get(url).then((res) => {
        setClassInfo(res.data);
      });
    };
    getClass();
  }, [axios, param.tid]);

  const handleClickedClass = (id) => {
    const path = location.pathname.replace("/dashboard", "/class/view");
    navigate({
      pathname: path,
      search: createSearchParams({
        id: id,
      }).toString(),
    });
  };
  return (
    <Section>
      <Container>
        <Title>Dashboard</Title>
        {classInfo &&
          classInfo.map((data) => {
            return (
              <Classes
                key={data.id}
                onClick={() => handleClickedClass(data.classId)}
              >
                <ClassInformation classInfo={data} />
              </Classes>
            );
          })}
      </Container>
    </Section>
  );
};

export default Dashboard;

const Section = styled.section`
  margin-top: 68px;
  height: 90vh;
`;

const Container = styled.div`
  padding: 0.5rem;
`;

const Title = styled.h1`
  font-size: 1.4rem;
`;

const Classes = styled.div`
  cursor: pointer;
  margin-bottom: 0.5rem;
`;
